import React from "react";
import { useTranslation } from "react-i18next";
import { Box, Grid, Typography, Container, Link, Card } from "@mui/material";
import Carousel from "nuka-carousel";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import back from "../../assets/Arrows/back-arrow.svg";
import forward from "../../assets/Arrows/forward-arrow.svg";
import { NextButton, BackButton } from "../../utils/theme.js";

const AdvisorsMobile = (props) => {
  const { t } = useTranslation();
  return (
    <Grid
      container
      flexDirection="column"
      justifyContent="center"
      mb={10}
      mt={2}
      sx={{
        display: props.display,
      }}
    >
      <Typography variant="h2" mb={1.5}>
        {t("advisorsComponent.title")}
      </Typography>
      <Typography variant="h4" textAlign="center" mb={1.5}>
        {t("advisorsComponent.subTitle")}
      </Typography>
      <Container
        sx={{
          width: { xs: "95%", sm: "90%", md: "80%", lg: "65%" },
          height: "87%",
        }}
      >
        <Carousel
          autoplayInterval={15000}
          transitionMode="fade"
          wrapAround
          autoplay
          renderCenterLeftControls={({ previousSlide }) => (
            <BackButton onClick={previousSlide}>
              <img src={back} alt="back-button" height="auto" width="auto" />
            </BackButton>
          )}
          renderCenterRightControls={({ nextSlide }) => (
            <NextButton onClick={nextSlide}>
              <img src={forward} alt="next-button" height="auto" width="auto" />
            </NextButton>
          )}
          renderBottomCenterControls={() => null}
        >
          {props.advisors.map((member, index, array) => (
            <Grid
              container
              flexDirection="column"
              alignItems="center"
              key={`teamMemb:${member.linkedin}`}
            >
              <Grid
                container
                item
                md={12}
                justifyContent="center"
                alignItems="flex-start"
                paddingX={{ xs: 0, md: 20 }}
              >
                {index === 0 ? (
                  <Card style={{ marginRight: "-80px", marginTop: 105 }}>
                    <img
                      src={array[array.length - 1].image}
                      alt={array[array.length - 1].name}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                ) : (
                  <Card style={{ marginRight: "-80px", marginTop: 105 }}>
                    <img
                      src={array[index - 1].image}
                      alt={array[index - 1].name}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                )}
                <Box
                  width="220px"
                  height="fit-content"
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "space-between",
                    marginTop: "50px",
                  }}
                >
                  <img
                    src={member.image}
                    width="100%"
                    height="auto"
                    alt={member.name}
                  ></img>
                  <Typography variant="h3" textAlign="center" mt={3}>
                    {member.name}
                  </Typography>
                  <Typography
                    variant="h4"
                    textAlign="center"
                    width="80vw"
                    mt={1}
                  >
                    {member.title}
                  </Typography>
                </Box>
                {index === array.length - 1 ? (
                  <Card style={{ marginLeft: "-80px", marginTop: 105 }}>
                    <img
                      src={array[0].image}
                      alt={`advisorsCard:${index}`}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                ) : (
                  <Card style={{ marginLeft: "-80px", marginTop: 105 }}>
                    <img
                      src={array[index + 1].image}
                      alt={`secondAdvisorsCard:${index}`}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                )}
              </Grid>
              {member.linkedin && (
                <Link
                  target="_blank"
                  href={member.linkedin}
                  aria-label={member.linkedin}
                >
                  <LinkedInIcon
                    color="disabled"
                    fontSize="large"
                    sx={{ marginTop: 2.5 }}
                  />
                </Link>
              )}
            </Grid>
          ))}
        </Carousel>
      </Container>
    </Grid>
  );
};
export default AdvisorsMobile;
